@import url(https://fonts.googleapis.com/css2?family=Balsamiq+Sans&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Satisfy&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Alef&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Imprima&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  background-color: #f5f6fa;
}

.app {
  display: flex;
  justify-content: space-evenly;
  padding: 20px;
}



.app__header {
  display: flex;
  /* flex-direction: row; */
  align-items: center;
  margin-bottom: 20px;
  justify-content: space-between;
}

.app__header img {
    height: 50px;
    width: 50px;



}
.app__dropdown {
  background: white;
}

.app__header h1 {
  color: #fc3c3c;
  font-size: 2rem;
}

.app__right {
  display: flex;
  flex-direction: column;
}

.app__right .MuiCardContent-root {
  display: flex;
  flex-direction: column;
  flex-grow: 1;
}

.app__graph {
  flex-grow: 1;
}

.app__graphTitle {
  margin-top: 20px;
}

.app__left {
  flex: 0.9 1;
  /* take 90% of the screen */
}
.app__stats {
  display: flex;
  justify-content: space-between;
}

.app__states {
  margin: 20px;
}

@media(max-width: 990px) {
  .app {
    flex-direction: column;
  }
}


.infoBox {
    flex: 1 1;
    cursor: pointer;
}

.infoBox:hover{
        /* background-color: #9c9fa3; */
        transition: all .5s ease;
        -webkit-transform: translateY(-5px);
                transform: translateY(-5px);
        box-shadow: 5px 10px 18px rgba(100, 98, 98, 0.5);
        cursor: pointer;
}


.infoBox:not(:last-child) {
    margin-right: 15px;
}

.infoBoxSelected {
    border-top: 10px solid greenyellow;
}

.infoBox--Red {
    border-color: red;
}

infoBox__cases--green {
    color: lightgreen !important;
}

.infoBox__cases {
    font-weight: 600;
    color: #cc1034;
    font-size: 1.75rem;
    margin-bottom: 0.5rem;
}

.infoBox__total {
    color: #6c757d;
    font-weight: 700 !important;
    font-size: 1rem !important;
    margin-top: 15px !important;
}
.map {
    height: 500px;
    background-color: white;
    padding: 1rem;
    border-radius: 20px;
    margin-top: 16px;
    box-shadow: 0 0 8px -4px rgba(0,0,0,0.5);
}

.map .leaflet-container {
    height: 100%;
}

.info-flag img {
    width: 100px;
    border-radius: 5px;
}

.info-flag{
    height: 80px;
    width: 100%;
    background-size: cover;
    border-radius: 8px;
}

.info-container {
    width: 150px;
}

.info-confirmed,
.info-recovered,
.info-deaths {
    font-size: 16px;
    margin-top: 5px;
}

.info-name {
    font-weight: bold;
    font-size: 20px;
    color: #555;
}
.table {
    margin-top: 20px;
    overflow: scroll;
    height: 400px;
    color: #6a5d5d;
    /* overflow: hidden; */
    
}

.table tr {
    display: flex;
    justify-content: space-between;
}

.table_country {
    color: black;
    font-weight: bold;
    /* color: #f5f5f5 */
}

.table td {
    padding: 0.5rem;

}

.table .table--flag {
    height: 80px;
    width: 100%;
    background-size: cover;
    border-radius: 8px;
}

.table .table--flag img {
    width: 100px;
    border-radius: 5px;
}

.table tr:nth-of-type(odd) {
    background-color: #f3f2f8;
    /* overflow: auto; */
}

.table tr .table--flag img {
    width: 100px !important;
}

.table tr .table--flag {
    height: 80px !important;
    width: 100%;
    background-size: cover;
    border-radius: 8px;
}

.flag-table {
    height: 2rem;
    width: 4rem;
    background-size: cover;
    /* background-position: 50% 50%; */
    margin-left: 5px;
}

img.flag-table {
    border: 1px solid #A8AEBA;
}
img {
    max-width: 100%;
    max-height: 100%;
}

img {
    vertical-align: middle;
    border-style: none;
}

